import React, { Component } from 'react';
import {
    Button,
    Collapse,
    Navbar,
    NavbarToggler,
    NavbarBrand,
    Nav,
    NavItem,
    NavLink,
  
    UncontrolledDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem } from 'reactstrap';
    import Notification from './Notification';
    import MediaControl from './MediaControl'
    import NotificationCom from './NotificationCom'
    import ModalExample from './ModalExample'


    class Header extends Component {
   
    constructor(props) {
        super(props);
       

        this.state = {
            ignore: true,
            title: '',
            isOpen: false,
            notification : "HHHH"
      };

      
      }
   
    render() {
      return (
        <div>
          
         <Navbar color="faded" light expand="md">
          <NavbarBrand href="https://github.com/foso">Github</NavbarBrand>
          <NavbarToggler onClick={this.toggle} />
          <Collapse isOpen={this.state.isOpen} navbar>
            <Nav className="ml-auto" navbar>
         
            <NavItem  >
                <NavLink><MediaControl /></NavLink>
              </NavItem>
          
              <NavItem  onClick={this.openModal}>
                <NavLink>         <div><ModalExample /></div>
</NavLink>
              </NavItem>
            
             
            
              <NavItem  >
                <NavLink><NotificationCom /></NavLink>
              </NavItem>
            </Nav>
          </Collapse>
        </Navbar>
        
     
      

        </div>
      );
    }
     
       
   
  }
  
  export default Header;


